    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        this.loadBodan()
      }
    },
    getBodanState (payload) {
      for (let a in this.tabledata1.List) {
        for (let b in this.tabledata1.List[a]) {
          if (this.tabledata1.List[a][b].getApi) {
            for (let item in payload.result.dataList) {
              // handicap 盤口一樣，把 compensate, count, gold 參數塞到物件裡
              if (this.tabledata1.List[a][b].handicap === payload.result.dataList[item].handicap &&
                  this.tabledata1.List[a][b].type === payload.result.dataList[item].type) {
                if (payload.result.dataList[item].hasOwnProperty('handicap') && payload.result.dataList[item].handicap) this.tabledata1.List[a][b].handicap = payload.result.dataList[item].handicap
                if (payload.result.dataList[item].hasOwnProperty('compensate') && payload.result.dataList[item].compensate) this.tabledata1.List[a][b].compensate = payload.result.dataList[item].compensate
                if (payload.result.dataList[item].hasOwnProperty('count') && payload.result.dataList[item].count) this.tabledata1.List[a][b].count = payload.result.dataList[item].count
                if (payload.result.dataList[item].hasOwnProperty('gold') && payload.result.dataList[item].gold) this.tabledata1.List[a][b].gold = payload.result.dataList[item].gold
                if (payload.result.dataList[item].hasOwnProperty('switch') && payload.result.dataList[item].switch) this.tabledata1.List[a][b].switch = payload.result.dataList[item].switch
              }
            }
          }
        }
      }
      this.count_date = payload.result.count_date
      this.game_time = payload.result.game_time
      this.main_team = payload.result.main_team
      this.visit_team = payload.result.visit_team
      this.league_name = payload.result.league_name
      this.transType = payload.result.transType
    },
    'selectTimeItems.selected' (value) {
      clearInterval(this.interval)
      this.selectedTimer = value
      if (value === 0) {
        this.intervalTimer = 0
      } else if (value === 0 || value === 5 || value === 60 || value === 180) {
        this.intervalTimer = 0
        this.setIntervalTime()
      }
    }
  },
  mounted(){
    this.updateTableList()
  },
  methods: {
    ...mapActions({
      getBodan: 'api/getBodan'
    }),
    // 載入波膽資料
    loadBodan (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        occupy: this.$route.query.occupy,
        posting: this.$route.query.Posting,
        game_serial_number: this.$route.query.gameSN,
        game_type: this.$route.query.game_type
      }
      this.getBodan(params)
        .then(res => {
          if (res.status === 200) {
            // 載入狀態(結束)
            this.loadingStatus = false
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    hasData (value) {
      return !common.isEmpty(value)
    },
    /**
     * 秒數倒數
     * @params  [speed]  倒數秒數
     **/
    startCountdown (speed) {
      this.reciprocalItems.currentSpeed = speed
      this.reciprocalItems.updateListTimer = setInterval(() => {
        this.reciprocalItems.currentSpeed--
        // 秒數為負數，則重新倒數
        if (this.reciprocalItems.currentSpeed < 0) {
          // 清除 timer
          clearInterval(this.reciprocalItems.updateListTimer)
          // 重新倒數秒數
          this.startCountdown(this.reciprocalItems.confirmSpeed)
        }
      }, 1000)
    },
    // 啟動秒數倒數刷新表格資料
    updateTableList () {
      this.loadBodan()

      // 確定更新秒數
      this.reciprocalItems.confirmSpeed = this.reciprocalItems.selected

      // 清除 timer
      clearInterval(this.reciprocalItems.updateListTimer)

      // 秒數為負數，則不更新倒數功能
      if (this.reciprocalItems.selected < 0) {
        // 當前秒數設置空值
        this.reciprocalItems.currentSpeed = ''
        return
      }
      // 倒數秒數
      this.startCountdown(this.reciprocalItems.confirmSpeed)
    }
  }
}
